html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote,
pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q,
s, samp, small, strike, strong, sub, sup, tt, var, b, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, audio, canvas, details, figcaption, figure, footer, header, hgroup,
mark, menu, meter, nav, output, progress, section, summary, time, video {
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
  margin: 0;
  padding: 0;
  font-family: "Roboto",sans-serif;
}

article, aside, dialog, figure, footer, header, hgroup, nav, section, blockquote {
  display: block;
}

nav ul {
  list-style: none;
}

ol {
  list-style: decimal;
}

ul {
  list-style: disc;
}

ul ul {
  list-style: circle;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: none;
}

ins {
  text-decoration: underline;
}

del {
  text-decoration: line-through;
}

mark {
  background: none;
}

abbr[title], dfn[title], acronym[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

input[type=submit], input[type=button], button {
  margin: 0;
  padding: 0;
}

input, select, a img {
  vertical-align: middle;
}

b, strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

html {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
}

/* generic class */
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-wrap: break-word;
  word-break: break-word;
}

p {
  margin-bottom: 20px;
}

ul, ol {
  margin-left: 35px;
  margin-bottom: 20px;
}

dl {
  margin-bottom: 20px;
}

dl dt {
  font-weight: 700;
  margin-top: 10px;
}

dl dt:first-child {
  margin-top: 0;
}

dl dd {
  margin-left: 20px;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  margin-bottom: 20px;
}

figure {
  max-width: 100%;
}

.clear {
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
  zoom: 1;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  content: " ";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

/* wordpress core */
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter, div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption {
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

figcaption.wp-caption-text {
  margin-top: 20px;
}

.navigation {
  text-align: right;
  margin-bottom: 30px;
}

.navigation .screen-reader-text {
  display: none;
}

.navigation a, .navigation span {
  margin: 0 5px;
}

.post-password-form input[type="password"] {
  padding: 14px 20px;
  margin-left: 5px;
  margin-right: 6px;
}

/* layout */
.realfactory-body-outer-wrapper {
  position: relative;
  overflow: hidden;
}

.realfactory-body-wrapper.realfactory-with-frame {
  position: relative;
}

.realfactory-page-wrapper {
  clear: both;
}

body.realfactory-boxed .realfactory-body-wrapper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

body.realfactory-boxed .realfactory-body-wrapper.realfactory-left {
  margin-left: 0;
}

body.realfactory-boxed .realfactory-body-wrapper.realfactory-right {
  margin-right: 0;
}

body.realfactory-boxed.realfactory-boxed-border .realfactory-body-wrapper {
  box-shadow: 0 -5px 9px rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: 0 -5px 9px rgba(0, 0, 0, 0.25);
}

body.realfactory-boxed.realfactory-background-pattern {
  background-position: center;
  background-repeat: repeat;
}

body.realfactory-boxed.realfactory-background-image .realfactory-body-background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: center;
  background-size: cover;
}

.realfactory-container {
  margin-left: auto;
  margin-right: auto;
}

.realfactory-column-10 {
  float: left;
  width: 16.6666%;
}

.realfactory-column-12 {
  float: left;
  width: 20%;
}

.realfactory-column-15 {
  float: left;
  width: 25%;
}

.realfactory-column-20 {
  float: left;
  width: 33.3333%;
}

.realfactory-column-24 {
  float: left;
  width: 40%;
}

.realfactory-column-30 {
  float: left;
  width: 50%;
}

.realfactory-column-36 {
  float: left;
  width: 60%;
}

.realfactory-column-40 {
  float: left;
  width: 66.6666%;
}

.realfactory-column-45 {
  float: left;
  width: 75%;
}

.realfactory-column-48 {
  float: left;
  width: 80%;
}

.realfactory-column-50 {
  float: left;
  width: 83.3333%;
}

.realfactory-column-60 {
  float: left;
  width: 100%;
}

.realfactory-sidebar-right {
  float: right;
}

.realfactory-sidebar-center {
  float: none;
  display: inline-block;
}

.realfactory-content-area {
  padding-top: 60px;
  padding-bottom: 40px;
}

.realfactory-sidebar-area {
  padding-top: 60px;
}

.realfactory-line-height-0 {
  line-height: 0;
}

a, button {
  text-decoration: none;
  transition: background 300ms, color 300ms, border-color 300ms;
  -moz-transition: background 300ms, color 300ms, border-color 300ms;
  -o-transition: background 300ms, color 300ms, border-color 300ms;
  -webkit-transition: background 300ms, color 300ms, border-color 300ms;
}

img {
  max-width: 100%;
  height: auto;
}

blockquote {
  quotes: none;
  padding: 30px 30px 15px;
  font-size: 17px;
  margin-bottom: 35px;
  font-style: italic;
  margin-top: 35px;
  border-left-width: 2px;
  border-left-style: solid;
}

q {
  margin-right: 15px;
  display: inline-block;
  padding: 10px 20px;
  vertical-align: middle;
  border-left-width: 2px;
  border-left-style: solid;
}

.realfactory-media-image {
  line-height: 0;
  position: relative;
}

.realfactory-left-align {
  text-align: left;
}

.realfactory-center-align {
  text-align: center;
}

.realfactory-right-align {
  text-align: right;
}

.realfactory-body-wrapper {
  overflow: hidden;
  z-index: 1;
}

/* input */
input, textarea {
  border-width: 1px 1px 2px 1px;
  border-style: solid;
}

input:focus, textarea:focus {
  outline: none;
}

/* button */
.realfactory-button, input[type="button"], input[type="submit"], input[type="reset"] {
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.8px;
  padding: 15px 33px;
  display: inline-block;
  -webkit-appearance: none;
}

input[type="button"], input[type="submit"], input[type="reset"] {
  border-width: 0;
  border-style: solid;
  cursor: pointer;
  transition: background 300ms, color 300ms, border-color 300ms;
  -moz-transition: background 300ms, color 300ms, border-color 300ms;
  -o-transition: background 300ms, color 300ms, border-color 300ms;
  -webkit-transition: background 300ms, color 300ms, border-color 300ms;
}

input[type="button"]:focus, input[type="submit"]:focus, input[type="reset"]:focus {
  outline: none;
}

/**
 * 2.0 - Header
 */
/* preload */
.realfactory-page-preload {
  position: fixed;
  z-index: 999999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #fff;
  background-image: url("../images/preload.gif");
  background-position: center;
  background-repeat: no-repeat;
}

.realfactory-page-preload.realfactory-out {
  background-image: none;
}

/* top bar */
.realfactory-top-bar {
  position: relative;
  border-bottom-style: solid;
}

.realfactory-top-bar-background {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
}

.realfactory-top-bar-container {
  position: relative;
  margin-left: auto;
  margin-right: auto;
}

.realfactory-top-bar-left {
  float: left;
}

.realfactory-top-bar-right {
  float: right;
}

.realfactory-top-bar-right-text {
  display: inline;
}

.realfactory-top-bar-right-social {
  display: inline;
  margin-left: 10px;
}

.realfactory-top-bar-right-social a {
  margin-left: 25px;
}

/* mobile menu */
.realfactory-mm-menu-wrap {
  display: none;
}

.admin-bar .mm-menu {
  margin-top: 32px;
}

.realfactory-mobile-button-hamburger-with-border {
  font-size: 16px;
  padding: 6px;
  line-height: 0;
  width: 32px;
  height: 32px;
  text-align: center;
  display: inline-block;
  border-width: 2px;
  border-style: solid;
  border-radius: 3px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.realfactory-right-menu-button {
  display: inline-block;
  margin-left: 27px;
}

.realfactory-mobile-menu {
  float: left;
  margin-top: -8px;
  margin-left: 30px;
}

.realfactory-mobile-menu-right {
  position: absolute;
  right: 30px;
  top: 50%;
  margin-top: -9px;
}

/* hamburger */
.realfactory-mobile-button-hamburger {
  box-sizing: border-box;
  display: block;
  width: 32px;
  height: 32px;
  position: relative;
}

.realfactory-mobile-button-hamburger:before,
.realfactory-mobile-button-hamburger:after,
.realfactory-mobile-button-hamburger span {
  content: '';
  display: block;
  width: 22px;
  height: 2px;
  position: absolute;
  left: 5px;
}

.realfactory-mobile-button-hamburger:before {
  top: 8px;
}

.realfactory-mobile-button-hamburger span {
  top: 15px;
}

.realfactory-mobile-button-hamburger:after {
  top: 22px;
}

.realfactory-mobile-button-hamburger:before {
  transition-duration: 0.4s;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.realfactory-mobile-button-hamburger span {
  transition: opacity 0.15s 0.4s ease;
}

.realfactory-mobile-button-hamburger:after {
  transition: -webkit-transform 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: transform 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.realfactory-mobile-button-hamburger.realfactory-active:before {
  -webkit-transform: translate3d(0, 7px, 0) rotate(135deg);
  transform: translate3d(0, 7px, 0) rotate(135deg);
  transition-delay: 0.1s;
}

.realfactory-mobile-button-hamburger.realfactory-active span {
  transition-delay: 0s;
  opacity: 0;
}

.realfactory-mobile-button-hamburger.realfactory-active:after {
  transition-delay: 0.1s;
  -webkit-transform: translate3d(0, -7px, 0) rotate(-135deg);
  transform: translate3d(0, -7px, 0) rotate(-135deg);
}

/* navigation menu */
.realfactory-navigation .sf-menu {
  display: inline-block;
  line-height: 1.7;
  text-align: left;
  margin-bottom: 0;
}

.realfactory-navigation .sf-menu > li:first-child {
  padding-left: 0;
}

.realfactory-navigation .sf-menu > li:last-child {
  padding-right: 0;
}

.realfactory-navigation .sf-menu > li > a {
  letter-spacing: 0.5px;
}

.realfactory-navigation-submenu-indicator .sf-menu > li > a.sf-with-ul-pre:after {
  display: inline-block;
  margin-left: 13px;
  content: "\f107";
  font-family: "fontAwesome",sans-serif;
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu li {
  padding: 1px 11px;
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu li:first-child {
  padding-top: 12px;
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu li:last-child {
  padding-bottom: 12px;
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu li li {
  /* padding-left: 0; */
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu li > a {
  display: block;
  padding: 6px 12px;
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu li > a.sf-with-ul-pre:after {
  float: right;
  margin-left: 18px;
  content: "\f105";
  font-family: fontAwesome;
}

.realfactory-navigation .sf-menu > .realfactory-normal-menu .sub-menu {
  font-size: 14px;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-full {
  left: 0;
  right: 0;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section-wrap {
  overflow: hidden;
  border-top-width: 1px;
  border-top-style: solid;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section-wrap:first-child {
  border-top: none;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section {
  padding-bottom: 999px;
  margin-bottom: -999px;
  border-left-width: 1px;
  border-left-style: solid;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section:first-child {
  border-left: none;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section-inner {
  padding: 31px 28px;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section-inner > a {
  font-size: 15px;
  display: block;
  margin-bottom: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: bold;
  padding: 0 12px;
}

.realfactory-navigation .sf-menu > .realfactory-mega-menu .sf-mega-section-inner .sub-menu a {
  font-size: 14px;
  padding: 5px 12px;
  display: block;
}

.realfactory-navigation .realfactory-navigation-slide-bar {
  border-bottom-width: 4px;
  border-bottom-style: solid;
  position: absolute;
  display: none;
  margin-top: -4px;
  overflow: hidden;
}

.realfactory-navigation .sf-vertical > li {
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

.realfactory-navigation .sf-vertical > li:first-child {
  padding-top: 0;
}

.realfactory-navigation .sf-vertical > li > a {
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  display: block;
}

.realfactory-navigation .sf-vertical ul.sub-menu {
  font-size: 14px;
  box-sizing: content-box;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
}

.realfactory-navigation .sf-vertical ul.sub-menu li {
  padding: 2px 11px;
}

.realfactory-navigation .sf-vertical ul.sub-menu li:first-child {
  padding-top: 20px;
}

.realfactory-navigation .sf-vertical ul.sub-menu li:last-child {
  padding-bottom: 20px;
}

.realfactory-navigation .sf-vertical ul.sub-menu li > a {
  display: block;
  padding: 4px 12px;
}

.realfactory-navigation .sf-vertical ul.sub-menu li > a.sf-with-ul:after,
.realfactory-navigation-submenu-indicator .sf-vertical > li > a.sf-with-ul:after {
  float: right;
  margin-left: 18px;
  content: "\f105";
  font-family: fontAwesome;
}

/* bullet anchor */
.realfactory-bullet-anchor {
  position: fixed;
  top: 50%;
  right: 30px;
  margin-top: -50px;
  z-index: 10;
  opacity: 0;
  transition: opacity 200ms;
  -moz-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  -webkit-transition: opacity 200ms;
}

.realfactory-bullet-anchor.realfactory-init {
  opacity: 1;
}

.realfactory-bullet-anchor a {
  display: block;
  width: 17px;
  height: 17px;
  margin-bottom: 5px;
  overflow: hidden;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}

.realfactory-bullet-anchor a:before {
  content: " ";
  width: 9px;
  height: 9px;
  margin: 3px;
  display: block;
  overflow: hidden;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  transition: background-color 200ms;
  -moz-transition: background-color 200ms;
  -o-transition: background-color 200ms;
  -webkit-transition: background-color 200ms;
}

/* breadcrumbs */
.realfactory-breadcrumbs a {
  color: #c0c0c0;
}

.realfactory-breadcrumbs,
.realfactory-breadcrumbs a:hover {
  color: #777;
}

.realfactory-breadcrumbs {
  font-size: 15px;
  padding: 21px 0 19px;
}

.realfactory-breadcrumbs span[property="itemListElement"] {
  margin: 0 15px;
}

.realfactory-breadcrumbs span[property="itemListElement"]:first-child {
  margin-left: 0;
}

.realfactory-breadcrumbs a span {
  transition: color 200ms;
  -moz-transition: color 200ms;
  -o-transition: color 200ms;
  -webkit-transition: color 200ms;
}

/* top search */
.realfactory-top-search-wrap input.search-field {
  border-bottom-color: #303030;
  color: #fff;
}

.realfactory-top-search-wrap .realfactory-top-search-submit,
.realfactory-top-search-wrap .realfactory-top-search-close {
  color: #fff;
}

.realfactory-top-search-wrap input::-webkit-input-placeholder {
  color: #fff;
}

.realfactory-top-search-wrap input::-moz-placeholder {
  color: #fff;
}

.realfactory-top-search-wrap input:-ms-input-placeholder {
  color: #fff;
}

.realfactory-top-search-wrap input:-moz-placeholder {
  color: #fff;
}

.realfactory-top-search-row {
  display: table;
  width: 100%;
  height: 100%;
}

.realfactory-top-search-cell {
  display: table-cell;
  vertical-align: middle;
  padding-left: 30px;
  padding-right: 30px;
}

.realfactory-top-search-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999;
  overflow-y: auto;
  display: none;
}

.realfactory-top-search-wrap form {
  max-width: 630px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.realfactory-top-search-wrap input.search-field {
  background: transparent;
  width: 100%;
  border-width: 0 0 3px;
  font-size: 40px;
  padding: 0 120px 25px 0;
  line-height: 48px;
  letter-spacing: 0.5px;
}

.realfactory-top-search-wrap input.search-submit {
  position: absolute;
  top: 0;
  right: 52px;
  width: 48px;
  height: 48px;
  padding: 0;
  opacity: 0;
  filter: alpha(opacity=0);
}

.realfactory-top-search-wrap .realfactory-top-search-submit {
  font-size: 30px;
  position: absolute;
  right: 52px;
  top: 0;
  line-height: 48px;
  width: 48px;
  text-align: center;
}

.realfactory-top-search-wrap .realfactory-top-search-close {
  font-size: 48px;
  position: absolute;
  right: 0;
  top: 0;
  line-height: 48px;
  width: 48px;
  text-align: center;
  cursor: pointer;
}

/* overlay menu */
.realfactory-overlay-menu-row {
  display: table;
  width: 100%;
  height: 100%;
}

.realfactory-overlay-menu-cell {
  display: table-cell;
  vertical-align: middle;
}

.realfactory-overlay-menu-content {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 9999;
  overflow-y: auto;
  display: none;
}

.realfactory-overlay-menu-content ul.menu {
  list-style: none;
  text-align: center;
  padding-top: 140px;
  padding-bottom: 180px;
  max-width: 570px;
  margin-left: auto;
  margin-right: auto;
}

.realfactory-overlay-menu-content ul.menu > li {
  font-size: 23px;
  font-weight: bold;
  text-transform: uppercase;
  opacity: 0;
  margin-bottom: 17px;
  padding-bottom: 17px;
