html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote,
pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q,
s, samp, small, strike, strong, sub, sup, tt, var, b, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, audio, canvas, details, figcaption, figure, footer, header, hgroup,
mark, menu, meter, nav, output, progress, section, summary, time, video {
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
  margin: 0;
  padding: 0;
  font-family: "Roboto",sans-serif;
}

article, aside, dialog, figure, footer, header, hgroup, nav, section, blockquote {
  display: block;
}

nav ul {
  list-style: none;
}

ol {
  list-style: decimal;
}

ul {
  list-style: disc;
}

ul ul {
  list-style: circle;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: none;
}

ins {
  text-decoration: underline;
}

del {
  text-decoration: line-through;
}

mark {
  background: none;
}

abbr[title], dfn[title], acronym[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0;
}

input[type=submit], input[type=button], button {
  margin: 0;
  padding: 0;
}

input, select, a img {
  vertical-align: middle;
}

b, strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

html {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  -moz-box-sizing: inherit;
  -webkit-box-sizing: inherit;
}

/* generic class */
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  word-wrap: break-word;
  word-break: break-word;
}

p {
  margin-bottom: 20px;
}

ul, ol {
  margin-left: 35px;
  margin-bottom: 20px;
}

dl {
  margin-bottom: 20px;
}

dl dt {
  font-weight: 700;
  margin-top: 10px;
}

dl dt:first-child {
  margin-top: 0;
}

dl dd {
  margin-left: 20px;
}

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  margin-bottom: 20px;
}

figure {
  max-width: 100%;
}

.clear {
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
  zoom: 1;
}

.clearfix {
  zoom: 1;
}

.clearfix:after {
  content: " ";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

/* wordpress core */
.alignnone {
  margin: 5px 20px 20px 0;
}

.aligncenter, div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}

.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}

a img.alignnone {
  margin: 5px 20px 20px 0;
}

a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}

a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto
}

.wp-caption {
  text-align: center;
}

.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}

.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}

.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}

figcaption.wp-caption-text {
  margin-top: 20px;
}

.bypostauthor {
}

.gallery-caption {
}

.sticky {
}

.navigation {
  text-align: right;
  margin-bottom: 30px;
}

.navigation .screen-reader-text {
  display: none;
}

.navigation a, .navigation span {
  margin: 0 5px;
}

.post-password-form input[type="password"] {
  padding: 14px 20px;
  margin-left: 5px;
  margin-right: 6px;
}

/* layout */
.realfactory-body-outer-wrapper {
  position: relative;
  overflow: hidden;
}

.realfactory-body-wrapper.realfactory-with-frame {
  position: relative;
}

.realfactory-page-wrapper {
  clear: both;
}

body.realfactory-boxed .realfactory-body-wrapper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

body.realfactory-boxed .realfactory-body-wrapper.realfactory-left {
